"use client";
import { PlayerContext } from "@/context/playerContext";
import useCookies from "@/hooks/useCookies";
import { PauseIcon, PlayCircleIcon, PlayIcon } from "lucide-react";
import React, { useContext, useEffect, useState } from "react";
import { PlaybackState } from "spotify-web-playback-sdk";

const PlayButton = ({
	isShowing,
	player,
	handlePlayButtonClick,
}: {
	isShowing: boolean;
	handlePlayButtonClick: () => void;
	player: Spotify.Player;
}) => {
	const { getCookie, setCookie } = useCookies();
	const { play, spotifyTrack, setIsNextSongLoading } =
		useContext<any>(PlayerContext);

	const [playerState, setPlayerState] = useState<PlaybackState | null>();

	useEffect(() => {
		if (!player) return;
		player.getCurrentState().then((state) => {
			console.log(state);
			setPlayerState(state);
		});
	}, []);

	useEffect(() => {
		player.getCurrentState().then((state) => {});
	}, [player]);

	const handlePlayPause = () => {
		handlePlayButtonClick();
		console.log(spotifyTrack);
		play();
		if (spotifyTrack) {
			player.getCurrentState().then((state) => console.log(state));
		}
	};

	return (
		<div
			className={`play-button ${isShowing ? "showing" : ""}`}
			onClick={handlePlayPause}
		>
			{playerState?.isPaused ? <PauseIcon /> : <PlayIcon />}
		</div>
	);
};

export default PlayButton;
