"use client";

import useCookies from "@/hooks/useCookies";
import { getSpotifyTrackIdByArtistAndTitle } from "@/services/Spotify";
import { SongData } from "@/types/types";
import { createContext, useState, useEffect, useRef } from "react";
import { toast } from "sonner";
import Spotify from "spotify-api.js";
export const PlayerContext = createContext({});

export const PlayerProvider = ({ children }: { children: React.ReactNode }) => {
	const [player, setPlayer] = useState<Spotify.Player | null>(null);
	const [currentTrack, setCurrentTrack] = useState<SongData | undefined>(
		undefined
	);
	const [isPlaying, setIsPlaying] = useState(false);
	const [isPaused, setIsPaused] = useState(false);
	const [spotifyTrack, setSpotifyTrack] = useState<any>("");
	const [deviceId, setDeviceId] = useState("");
	const [active, setActive] = useState(false);
	const [isNextSongLoading, setIsNextSongLoading] = useState(false);

	const { setCookie, getCookie } = useCookies();

	// =====================
	// Initialize player SKD
	// =====================

	useEffect(() => {
		const script = document.createElement("script");
		script.src = "https://sdk.scdn.co/spotify-player.js";
		script.async = true;
		document.body.appendChild(script);

		(window as any).onSpotifyWebPlaybackSDKReady = () => {
			const token = JSON.parse(getCookie("token") || "{}")?.access_token;
			if (!token) {
				console.warn("No access token found in cookies.");
				return;
			}

			const player = new (window as any).Spotify.Player({
				name: "PhilPlaysBass app",
				getOAuthToken: (cb: any) => cb(token),
				volume: 0.5,
			});

			setPlayer(player);

			player.addListener("ready", async ({ device_id }: any) => {
				console.log("Ready with Device ID", device_id);

				setDeviceId(device_id);
				setCookie("device_id", device_id);

				const pb = await transferPlayback(device_id, token);
				console.log("playback transferred to device", pb);
			});

			player.addListener("not_ready", ({ device_id }: any) => {
				console.log("Device ID has gone offline", device_id);
			});

			player.addListener("player_state_changed", (state: any) => {
				if (!state) {
					return;
				}

				player.getCurrentState().then((state: any) => {
					setIsPaused(state.paused);
					setIsNextSongLoading(state.loading);
				});
			});

			player.connect().then((success: boolean) => {
				if (success) {
					console.log("The web player sucessfully connected to Spotify");
				}
			});
		};

		return () => {
			delete (window as any).onSpotifyWebPlaybackSDKReady;
		};
	}, []);

	console.log(isNextSongLoading, spotifyTrack);

	// =================
	// Fetch next song
	// =================
	useEffect(() => {
		const data = getCookie("token");
		setIsNextSongLoading(true);

		if (!data) {
			console.warn("No token cookie found");
			return;
		}

		try {
			const tokenObject = JSON.parse(data);
			if (currentTrack && tokenObject?.access_token) {
				(async () => {
					try {
						const result: any = await getSpotifyTrackIdByArtistAndTitle(
							currentTrack.song_title,
							tokenObject.access_token
						);
						if (result) {
							setSpotifyTrack(result);
						} else {
							toast("Not found", {
								description: `Spotify did not find '${currentTrack.song_title}' by '${currentTrack.artist.name}'`,
								className: "not-found_toast",
							});
						}
					} catch (err) {
						console.error("Error fetching Spotify track ID:", err);
					}
				})();
			}
		} catch (err) {
			console.error("Failed to parse token cookie:", err);
		}
	}, [currentTrack]);

	// ================================================
	// Necessary to ensure the app can control playback
	// ================================================
	async function transferPlayback(device_id: string, token: string) {
		await fetch("https://api.spotify.com/v1/me/player", {
			method: "PUT",
			body: JSON.stringify({
				device_ids: [device_id],
			}),
			headers: {
				"Content-Type": "application/json",
				Authorization: `Bearer ${token}`, // use a valid token
			},
		});
	}

	async function play() {
		const token = JSON.parse(getCookie("token") || "{}")?.access_token;
		const device_id = getCookie("device_id");

		if (!token || !device_id) {
			console.error("Missing token or deviceId");
			return;
		}

		await fetch(
			`https://api.spotify.com/v1/me/player/play?device_id=${device_id}`,
			{
				method: "PUT",
				headers: {
					Authorization: `Bearer ${token}`,
					"Content-Type": "application/json",
				},
				body: JSON.stringify({ uris: [spotifyTrack?.uri] }),
			}
		).then(() => setIsNextSongLoading(false));
	}

	async function pause() {
		const token = JSON.parse(getCookie("token") || "{}")?.access_token;

		if (!player || !token) {
			console.error("Missing player instance or token");
			return;
		}

		try {
			player.pause();
			if (!player) {
				console.warn(
					"No active playback state. Falling back to Web API pause."
				);
				await fetch("https://api.spotify.com/v1/me/player/pause", {
					method: "PUT",
					headers: {
						Authorization: `Bearer ${token}`,
						"Content-Type": "application/json",
					},
				});
				return;
			}

			await player.pause();
			console.log("Playback paused using SDK.");
		} catch (err) {
			console.error("Error pausing via SDK. Falling back to Web API:", err);

			try {
				await fetch("https://api.spotify.com/v1/me/player/pause", {
					method: "PUT",
					headers: {
						Authorization: `Bearer ${token}`,
						"Content-Type": "application/json",
					},
				});
				console.log("Playback paused using Web API fallback.");
			} catch (apiErr) {
				console.error("Failed to pause using Web API:", apiErr);
			}
		}
	}

	return (
		<PlayerContext.Provider
			value={{
				player,
				currentTrack,
				setIsPlaying,
				setCurrentTrack,
				isPlaying,
				isPaused,
				setIsPaused,
				play,
			}}
		>
			{children}
		</PlayerContext.Provider>
	);
};
